<template>
  <el-dialog title="" top="5vh" width="55%" :visible.sync="dialogFormVisible" @close="diaClose" :close-on-click-modal="false" append-to-body>
    <el-form label-position="right" label-width="90px" ref="ruleForm" size="small">
      <el-form-item prop="ip" label="">
        <el-input style="width:45%" v-model="ip" clearable disabled>{{ip}}</el-input>
      </el-form-item>
    </el-form>
    <div class="statusTip">
      <span>{{this.status}}</span>
      &nbsp;&nbsp; <span v-show="this.getOldBtn == true"><el-button size="small" @click="getOldList"></el-button></span>
    </div>
    <el-table :data="tableData.slice((currentPage2-1)*pageSize2,currentPage2*pageSize2)" stripe tooltip-effect="light" style="width: 100%;border:1px solid #F0F2F5"
    :header-cell-style="{height: '48px','background-color': '#F0F2F5',color: 'rgba(0, 0, 0, 0.72)',padding:'0px'}" max-height="55vh">
      <template slot="empty">
        <img style="" v-if="emptytxt" src="@/assets/nodata1.png" />
      </template>
      <el-table-column type="index" :index="indexMethod" label="" width="50px"></el-table-column>
      <el-table-column prop="stage" label="" min-width="100%">
        <template slot-scope="scope">
          <span v-if="scope.row.stage >= 0 && scope.row.stage <= 999">TPCM(BIOS,UBOOT)</span>
          <span v-else-if="scope.row.stage >= 1000 && scope.row.stage <= 1999">(GRUB)</span>
          <span v-else-if="scope.row.stage >= 2000 && scope.row.stage <= 2999">(GRUB)</span>
          <span v-else-if="scope.row.stage >= 3000"></span>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" min-width="150%"></el-table-column>
      <el-table-column prop="result" label="" min-width="100%">
        <template slot-scope="props">
          <span v-if="props.row.result =='0'"></span>
          <span v-else-if="props.row.result =='1'"></span>
          <span v-else-if="props.row.result =='2'"></span>
        </template>
      </el-table-column>
      <el-table-column prop="hash" label="hash" min-width="150%"></el-table-column>
      <!-- <el-table-column prop="measureTime" label="" min-width="115%"></el-table-column> -->
    </el-table>
      <el-pagination
        @size-change="handleSizeChange2" class="paging" align='right'
        @current-change="handleCurrentChange2"
        :current-page="currentPage2"
        :page-sizes="[10,20]" 
        :page-size="pageSize2"
        layout="total, prev, pager, next, jumper"
        :total="total2">
      </el-pagination>
  </el-dialog>
</template>

<script>
import request from '@/utils/request'
import { timeToZh } from "@/utils/timefilter";

export default {
  name: 'ConfigPop',
  data () {
    return {
      dialogFormVisible: false,
      emptytxt:'',
      tableData: [],
      currentPage2: 1, // 
      total2: 0, // 
      pageSize2: 10,// 
      nodeId:'',
      autorefresh: '',//
      timer:'',
      status:'',
      getOldBtn:false,
      recordType:'',
      ip:''
    }
  },
  methods: {
    diaOpen (param) {
      var that = this
      that.ip = param.ip
      that.emptytxt = ''
      that.dialogFormVisible = true
      that.tableData = []
      that.recordType = ''
      that.nodeId= param.nodeId
      that.getOldBtn = false
      that.status = ""
      request({url: "/sec/lBMeasureRecord",method: "post", data:{node_id:param.nodeId}})
      .then((response) => {
        var res = response.data
        // console.log(res)
        res.list.map(function(value,index) {
          value.measureTime = timeToZh(value.measureTime);
        })
        that.tableData = res.list
      });
      that.initData()
      if (JSON.stringify(that.tableData) =='[]') {
        that.timer = setInterval( function(){
          if (JSON.stringify(that.tableData) !='[]' && JSON.stringify(that.tableData) != undefined && that.tableData.length > 0 ) { 
            // console.log(222233333,JSON.stringify(that.tableData))
            that.status = ""
            that.getOldBtn = false
          }
        },2000);
      } else {
        clearInterval(that.autorefresh)
        clearInterval(that.timer)
        that.status = ""
        that.getOldBtn = false
      }
      setTimeout(function() {
        clearInterval(that.autorefresh)
        clearInterval(that.timer)
        if (JSON.stringify(that.tableData) =='[]' || JSON.stringify(that.tableData) == undefined) {
          // console.log(3333,JSON.stringify(that.tableData))
          that.status = ""
          that.getOldBtn = true
        } else { 
          // console.log(2222,JSON.stringify(that.tableData))
          that.status = ""
          that.getOldBtn = false
        }
      },5000)
    },
    diaClose () {
      var that = this
      clearInterval(this.autorefresh);
      that.dialogFormVisible = false
    },
    indexMethod(index) {
      if (this.currentPage2 === 1) {
        if(index < 9) {
          return '0' + ( index + 1 )
        } else {
          return index + 1
        }
      } else {
        return (index+1)+this.pageSize2*(this.currentPage2-1)
      }
    },
    handleSizeChange2 (val) {
      // console.log('1', val)
      this.pageSize2 = val
      if(that.recordType == 'list') {
        that.getList()
      } else if (that.recordType == 'old') {
        that.getOldList()
      }
    },
    handleCurrentChange2 (val) {
      // console.log('2', val)
      this.currentPage2 = val
      if(that.recordType == 'list') {
        that.getList()
      } else if (that.recordType == 'old') {
        that.getOldList()
      }
    },
    getList() {
      var that = this
      request({url: "/sec/lBMeasureRecordList",method: "post", data: {node_id:that.nodeId}})
      .then((response) => {
        var res = response.data
        // console.log(res)
        if(res.list.length > 0) {
          that.recordType = 'list'
          that.total2 = res.total
          res.list.map(function(value,index) {
            value.measureTime = timeToZh(value.measureTime);
          })
          that.tableData = res.list
          clearInterval(this.autorefresh);
        }
      });
    },
    getOldList() {
      var that = this
      request({url: "/sec/lBMeasureRecordListOld",method: "post", data: {node_id:that.nodeId}})
        .then((response) => {
          var res = response.data
          // console.log(res)
          if(Object.keys(res.list).length != 0) {
            res.list.map(function(value,index) {
              value.measureTime = timeToZh(value.measureTime);
            })
            that.tableData = res.list
            that.total2 = res.total
            that.recordType = 'old'
          } 
          if(res.total ==0) {
            that.emptytxt = true
          } else {
            that.emptytxt = false
          }
        });
    },
    initData() {
      var that = this;
      that.autorefresh = setInterval(function () {
        that.getList()
      }, 1000);
    },
  },
  destroyed() {
    clearInterval(this.autorefresh);
    clearInterval(this.timer)
  },
}
</script>

<style scoped>
.statusTip {
  margin-bottom: 20px;
  /* border: 1px solid #; */
}
.rowstyle {
  border-left:1px solid #ebeef5;
  border-right:1px solid #ebeef5;
  border-bottom:1px solid #ebeef5;
}
.rowstyle:nth-of-type(odd) {
  background-color:#fafafa
}
.rowstyle:hover{
  background-color: #CAD4E6 !important;
  cursor: pointer;
}
.itemType {
  width:175px;
  text-align:left;
  float:left;
  border-right:1px solid #ebeef5;
  height:35px;
  line-height:35px;
  padding:0px 10px;
}
.itemValue {
  float:left;
  height:35px;
  line-height:35px;
  padding:0px 10px;
}
.paging {
  margin: 18px 0;
  color: rgba(51,51,51,0.9);
}
.paging >>>.el-pager li {
  min-width: 31px;
  height: 31px;
  line-height: 31px;
  border-radius: 2px;
  border: 1px solid #e4e4e4;
  margin: 0 3px;
}
.paging >>>.el-pager li.active, .paging >>>.el-pager li:hover{
  background-color: #effff8;
  border: 1px solid #1ac492;
  color: #1ac492;
  font-size: 14px;
}
.paging >>> .btn-next, .paging >>> .btn-prev {
  min-width: 31px;
  height: 31px;
  line-height: 31px;
  border: 1px solid #e4e4e4;
  padding: 0;
  margin: 0 5px;
}
.paging >>>.el-select .el-input__inner:focus,
.paging >>>.el-pagination__sizes .el-input .el-input__inner:hover,
.paging >>>.el-pagination__sizes .el-input .el-input__inner:active,
.paging >>>.el-pagination__sizes .el-select .el-input.is-focus .el-input__inner{
  border-color: #1ac492;
}

.paging >>>.el-pagination__jump {
  margin-left: 0px;
}
.paging >>>.el-pagination__jump .el-input__inner:focus {
  border: 1px solid #1ac492;
  margin-left: 0px;
}
.el-table-add-row {
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
  width: 100%;
  border-radius: 3px;
}
.dialog-footer .sureBtn {
  background-color: #1ac492!important;
  border: none;
  border: 1px solid #1ac492!important;
  color: #fff;
}
</style>
